/* Style for the signup page */
.signup-container {
    max-width: 500px;
    margin: 20px auto;
    padding: auto;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.signup-container h1 {
    text-align: center;
    background-color: #282c34;
    color: white;
    padding: 20px;
    margin-bottom: 20px;
    border-radius: 10px 10px 0 0;
}

.create-an-account {
    text-align: center;
    background-color: #282c34;
    color: white;
    padding: 20px;
    border-radius: 10px 10px 0 0;
}

.signup-container h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

.form-group {
    margin-bottom: 15px;
    padding: 0 20px;
}

.form-group label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
    color: #555;
}

.form-group input {
    width: 100%;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
}

button[type="submit"] {
    display: block;
    width: 50%;
    padding-bottom: 10px;
    margin: 0 auto 50px;
    font-size: 16px;
    background-color: #4CAF50;
    color: white;
    border: none;
    border-radius: 4px;
    cursor: pointer;
}

button[type="submit"]:hover {
    background-color: #45a049;
}

/* Added new login link styles that match the existing design */
.login-link {
    text-align: center;
    margin-bottom: 20px;
    color: #555;
    font-size: 14px;
}

.login-link a {
    color: #4CAF50;
    text-decoration: none;
    margin-left: 5px;
}

.login-link a:hover {
    text-decoration: underline;
}

/* Add focus state for better user experience */
.form-group input:focus {
    outline: 1px solid #4CAF50;
    border-color: #4CAF50;
}

/* Style for placeholders */
.form-group input::placeholder {
    color: #999;
    font-size: 14px;
}